<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
export default {
  name: "App"
};
</script>

<style>
@font-face {font-family: 'noto-regular'; src: url('assets/fonts/notokr-regular.eot'), url('assets/fonts/notokr-regular.woff') format('woff'), url('assets/fonts/notokr-regular.ttf') format('truetype');}
@font-face {font-family: 'noto-light'; src: url('assets/fonts/notokr-light.eot'), url('assets/fonts/notokr-light.woff') format('woff'), url('assets/fonts/notokr-light.ttf') format('truetype');}
@font-face {font-family: 'noto-bold'; src: url('assets/fonts/notokr-bold.eot'), url('assets/fonts/notokr-bold.woff') format('woff'), url('assets/fonts/notokr-bold.ttf') format('truetype');}
@font-face {font-family: 'noto-medium'; src: url('assets/fonts/notokr-medium.eot'), url('assets/fonts/notokr-medium.woff') format('woff'), url('assets/fonts/notokr-medium.ttf') format('truetype');}
body, p, ul, ol, li, dl, dt, dd, table, th, td, form,fieldset, legend, input, textarea, button, select
{
margin: 0;padding: 0;font-family: 'noto-regular', 'nx-regular', "Nanum Gothic",'돋움',Dotum,AppleGothic,sans-serif; font-size: 14px; color: #fff
}
img, fieldset {border: 0}
ul, ol {list-style: none}
em, address {font-style: normal}
a {text-decoration: none}
a:hover, a:active, a:focus {text-decoration: none}
html, body, #app{height: 100%}
@keyframes an{
  from{opacity: 0;transform: perspective(500px) translate3d(-35px, -40px, -150px) rotate3d(1, -1, 0, 35deg);}
  to{opacity: 1;transform: perspective(500px) translate3d(0, 0, 0);}
}
</style>



